class OnHoldList extends FlowComponent {
    state = {
        refresh: false,
        rowdatas:[],
        columndatas:[],
        copy:[],        
        holdALLcount:0,
		holdUMTNcount:0,
		holdUEZMcount:0,
		holdCCAcount:0,
        faPendingdisb: localStorage.getItem('faPendingdisb') !=null ? JSON.parse(localStorage.getItem('faPendingdisb')):'ALL',
    }    

    componentWillMount() {
        this.server_time(this.time_zone)
        this.drawTable()        
        // setInterval(() => this.refreshList(),60000)                                      
    }    

    async drawTable(){
        await this.server_time(this.time_zone)
        const loan_search_criteria = {status : "stalled", disburse_attempt: true, mode : 'search'};
        searchHeldLoan(this.req({loan_search_criteria}, true))
        .then((response) => {                                                     
            this.resp(response)
            if(!response){return};
            if(response){                            
                this.setState(state => state.rowdatas = response.data.results)
                this.setState(state => state.copy = response.data.results)
                this.setState(state => state.columndatas = this.columndatas()) 
                this.setCount(response)  
                this.filterHandler(this.state.faPendingdisb)                                                                                                                                         
                // var loanResults =[];                                                                
                // loanResults = response.data.results.map((loan)=> <tr>

                //     <td>{loan.acc_prvdr_code}</td>
                //     <td><Link to={"/fa/view/"+loan.loan_doc_id} target="_blank">{loan.loan_doc_id}</Link></td>
                //     <td><Link to={"/borrower/indiv/view/"+loan.cust_id} target="_blank">{loan.acc_number}</Link></td>
                //     <td><TooltipLabel full_text={loan.biz_name} overlay={loan.cust_name} no_reduce={true} /></td>
                //     <td>{lbl_amt(loan.loan_principal,this.currency_code)}</td>
                //     <td>{loan.attempts}</td>
                //     <td>{<OverlayTrigger placement='auto' trigger="hover" overlay={  <Popover style={{fontSize: "15px", padding: "15px", color:"mediumblue", fontFamily: "Montserrat"}}> <LoanDates data={loan}/> </Popover>}><span>{get_time_diff(this.state.server_time.server_time_db, loan.attempt_time)} <sup><FcInfo size={14}/></sup></span></OverlayTrigger>}</td>
                //     <td>{title_case(loan.status)}</td>
                //     <td><OverlayTrigger placement='auto' trigger="hover" overlay={  <Popover style={{fontSize: "15px", padding: "15px", color:"mediumblue", fontFamily: "Montserrat"}}>{getOverlayInfo(loan.disbursal_status, loan)}</Popover>}><span>{title_case(loan.disbursal_status || "not_initiated")} <sup><FcInfo size={14}/></sup></span></OverlayTrigger></td>
                //     <td>
                //         {this.buttonName(loan)}
                //     </td>
                    
                //     </tr>
                //     ); 
           
                //     this.setState({loanResults: loanResults, refresh: false});

                //         $('#loan_list-b').removeAttr('width').dataTable({
                //             "order" : [[8,'desc'],[6,'desc']],
                //             "columns": [
                //                 {"width": "5%"}, 
                //                 {"width": "5%"}, 
                //                 {"width": "5%"},
                //                 {"width": "5%"},
                //                 {"width": "5%"},
                //                 {"width": "5%"},
                //                 {"width": "5%"},
                //                 {"width": "5%"},
                //                 {"width": "5%"},
                //                 {"width": "5%"},
                //              ],
                //             "searching": false,
                //             "paging": false,
                //             "info": false,
                //     });
                }
        });
    }

    getOverlayInfo (disb_status, loan) {
        let msg
        if (disb_status === "failed"){
            msg = "Float not transferred. App Support Will Retry"
        }
        else if (disb_status === "capture_failed"){
            msg = "Float Transferred. Not Captured on Flow App. Customer Success will Capture"
        }
        else if (disb_status === "unknown"){
            msg = "App Support Will Process"
        }
        else if (disb_status === "sent_to_disbursal_queue"){
            msg = "Float will be transferred shortly"
        }
        else if (disb_status === null && !loan.customer_consent_rcvd){
            msg = "Customer has not sent the confirmation code"
        }
        return msg                
    } 

    columndatas () {
        return [
            {
                Header : "ACC PRVDR",
                id: "acc_prvdr_code",
                accessor : "acc_prvdr_code"
            },
            {
                Header : "FA ID",
                id: 'loan_doc_id',
                accessor: row => <Link to={"/fa/view/"+row.loan_doc_id} target="_blank">{row.loan_doc_id}</Link>
            },
            {
                Header : "A/C Number",
                id: 'acc_number',
                accessor: row =><Link to={"/borrower/indiv/view/"+row.cust_id} target="_blank">{row.acc_number}</Link>
            },
            {
                Header : "Mobile Number",
                id: 'cust_mobile_num',
                accessor: row => {return row.cust_mobile_num}
            },
            {
                Header : "Biz Name",
                id:'biz_name',
                accessor: row => <TooltipLabel full_text={row.biz_name} overlay={row.cust_name} no_reduce={true} />
            },
            {
                Header : () => (<div className={'tableAmount_centerAlign'}>FA Amount</div>),
                id:'loan_principal',
                accessor:row => <div className={'tableAmount_rightAlign'}>{lbl_amt(row.loan_principal,this.currency_code)}</div>
            },
            {
                Header : "Total Attempts",
                id:'attempts',
                accessor: row => <div className={'tableAmount_centerAlign'}>{row.attempts}</div>
            },
            {
                Header : "Attempted",
                id:'attempt_time',
                accessor : row => <OverlayTrigger placement='auto' trigger="hover" overlay={  <Popover style={{fontSize: "15px", padding: "15px", color:"mediumblue", fontFamily: "Montserrat"}}> <LoanDates data={row}/> </Popover>}><span>{get_time_diff(this.state.server_time.server_time_db, row.attempt_time)} <sup><FcInfo size={14}/></sup></span></OverlayTrigger>
            },
            {
                Header : "FA Status",
                id : 'status',
                accessor : row => {return title_case(row.status)}
            },
            {
                Header : "Disbursal Status",
                id: 'disbursal_status',
                accessor : row => <OverlayTrigger placement='auto' trigger="hover" overlay={  <Popover style={{fontSize: "15px", padding: "15px", color:"mediumblue", fontFamily: "Montserrat"}}>{this.getOverlayInfo(row.disbursal_status, row)}</Popover>}><span>{title_case(row.disbursal_status || "not_initiated")} <sup><FcInfo size={14}/></sup></span></OverlayTrigger>
            },
            {
                Header : "Action",
                id:'btn',
                accessor : row => this.buttonName(row)                
            },
        ]
    }

    statusChange (event,loan) {


        let value = event.target.value
        let alert_msg = ""
        if (value === 'capture_failed') {
            alert_msg = "Are you sure if the disbursal attempt is successful and there is a debit transaction to the customer found in the Data Provider A/C statement on " + loan.attempt_time+"?"
        } else if (value === 'failed') {
            alert_msg = "Are you sure the disbursal attempt got failed and there is no debit transaction to the customer found in the Data Provider A/C statement?"
        }
        if (window.confirm(alert_msg)) {
            const request = {loan_doc_id: loan.loan_doc_id, disbursal_status: value}
            changeDisbursalStatus(this.req(request)).then((res) => {
                this.resp(res)
                if (res && res.status == 'success') {
                    this.refreshList()
                }
            })

        }
    }

    refreshList = () => {
            this.setState({refresh: true})
            $('#loan_list-b').DataTable().destroy();
            this.drawTable()
    }


    buttonName = (loan) => {
        let value
        if(loan.disbursal_status == 'capture_failed' && check_priv('loan','home_capture_disbursal')){

           value =  <CaptureFailedDisbursal id="disbursal_txn" data={loan} title="Disbursal Loan" />
        }
        else if(loan.disbursal_status == 'failed' && check_priv('loan','retry_disbursal')){
           value = <input style={{marginBottom: '1%'}} id={loan.loan_doc_id} type='submit' value ='Retry' disabled={this.state.calling_api} onClick={(event) => {this.retryFA(loan.loan_doc_id)}} className='btn btn-primary btn-sm'/>
        }
        else if(loan.disbursal_status == 'unknown' && check_priv('loan','release')){
            value = <select style={{minHeight:'30px',width:90}} onChange={(event) => {this.statusChange(event,loan)}}>
                    <option disabled selected hidden className='text-white'>choose</option>
                    <option value="capture_failed" className='text-white'>Capture Failed</option>
                    <option value="failed" className='text-white'>Failed</option></select>
        }

        return  value
    }

    retryFA = (loan_doc_id) => {
        if(this.state.calling_api){return}
        if(!window.confirm("Have you verified in the Data Provider's A/C statement if there is no debit transaction found related to this FA?\n\nPlease note that clicking OK will re-attempt Disbursal")){return}
        retryDisbursal(this.req({'loan_doc_id' : loan_doc_id,}, true)).then((response) => {
            this.resp(response)
            window.location.reload()
        })


	}

    componentDidUpdate(prevProps, prevState, snapshot) {                              
        if(prevProps.refresh != this.props.refresh){
            this.refreshList()
        }                     
    }
    
    setCount (response) {
        this.setState(state => state.holdALLcount = 0)
        this.setState(state => state.holdCCAcount = 0)
        this.setState(state => state.holdUEZMcount = 0)
        this.setState(state => state.holdUMTNcount = 0)
        response.data.results.forEach(element => {            
            if(element.acc_prvdr_code === "UEZM"){
                this.setState(state => state.holdUEZMcount ++)
            }
            else if(element.acc_prvdr_code === "CCA"){
                this.setState(state => state.holdCCAcount ++)
            }
            else if (element.acc_prvdr_code === "UMTN") {
                this.setState(state => state.holdUMTNcount ++)
            }
            this.setState(state => state.holdALLcount ++)
        });                        
    }    

    filterHandler  (val)  {    
		localStorage.setItem("faPendingdisb",JSON.stringify(val))
		if(val=="ALL"){
			const newArr = this.state.rowdatas
			this.setState(state => state.copy = newArr)	
		}
		if(val=="UEZM"){
			const newArr = ([...this.state.rowdatas.filter(each => each.acc_prvdr_code === val)])
            console.log(newArr,"newArr from filtering")			
			this.setState(state => state.copy = newArr)
		}
		else if(val=="UMTN"){
			const newArr = ([...this.state.rowdatas.filter(each => each.acc_prvdr_code === val)])			
			this.setState(state => state.copy = newArr)			
		}
		else if(val=="CCA"){
			const newArr = ([...this.state.rowdatas.filter(each => each.acc_prvdr_code === val)])			
			this.setState(state => state.copy = newArr)
		}        
	}

    render(){
        const NullComponent = () => null;                     
        return (
        <div>
                <div className="card">                    
                  <div className="card-header card-header-icon card-header-primary d-flex">
                    <h4 className="card-title text-white tableHead_font no-margin">FAs Pending Disbursal</h4>
                    <div className='countREL'>
                        <button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("faPendingdisb")) == "ALL" ? "darkgray" : ''}} onClick={()=>this.filterHandler("ALL")}>ALL</button>
                        <div className='countABS'>
                            <label>{this.state.holdALLcount}</label>
                        </div>
                    </div>
                    <div className='countREL'>
                        <button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("faPendingdisb")) == "UEZM" ? "darkgray" : ''}} onClick={()=>this.filterHandler("UEZM")}>UEZM</button>
                        <div className='countABS'>
                            <label>{this.state.holdUEZMcount}</label>
                        </div>
                    </div>  
                    <div className='countREL'>                  
                        <button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("faPendingdisb")) == "UMTN" ? "darkgray" : ''}} onClick={()=>this.filterHandler("UMTN")}>UMTN</button>
                        <div className='countABS'>
                            <label>{this.state.holdUMTNcount}</label>
                        </div>
                    </div>
                    <div className='countREL'>
                        <button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("faPendingdisb")) == "CCA" ? "darkgray" : ''}} onClick={()=>this.filterHandler("CCA")}>CCA</button>
                        <div className='countABS'>
                            <label>{this.state.holdCCAcount}</label>
                        </div>
                    </div>
                    {/* {this.state.refresh && <h2 className="reloadText"><i class="fa fa-refresh"> </i> Refreshing...</h2>} */}
                    {/* {this.state.date && <h4 className="text_color pt-3">{this.state.date.toLocaleString('en-US', {hour: 'numeric', minute: 'numeric', hour12: true })}</h4>} */}                    
                  </div>
                  <div className="card-body">
                    <div className="table-responsive">
                      {/* <table className="table">
                        <thead className=" text-info">
                        <th>ACC PRVDR</th>
                        <th>FA ID</th>
                        <th>Account Number</th>
                        <th>Biz Name</th>
                        <th>FA Amount</th>
                        <th>Total Attempts</th>
                        <th>Attempted</th>
                        <th>FA Status</th>
                        <th>Disbursal Status</th>
                        <th>Action</th>
                        </thead>
                        <tbody>
                            {this.state.loanResults}
                        </tbody>
                      </table> */}                      
                      <ReactTable NoDataComponent={NullComponent} showPaginationBottom={false} minRows = {1}  data={this.state.copy}  
						columns={this.state.columndatas} 
						resizable={false} defaultSorted={[{id: "acc_prvdr_code",desc: true}]} />
                    </div>
                  </div>
                </div>
            </div>	
        );

}


}
export default OnHoldList;
