import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {borrowerSearch} from '../../actions/borrower_actions';
import ReactTable from "react-table";
import {format_date_tbl, capitalize_first_letter, get_time_diff, lbl_amt} from '../../helpers/common_helper';
import { Link} from 'react-router-dom';
import {searchLoan} from "../../actions/loan_actions";
import moment from "moment";
import {searchLoanAppl} from "../../actions/loan_appl_actions";
import {TooltipLabel} from "../common/partial";
import {FcRefresh} from "react-icons/all";
import {GrRefresh} from "@react-icons/all-files/gr/GrRefresh";
import { each } from 'jquery';







class DisplayList extends FlowComponent {

	state = {
		columns : [],
		lists : [],	
		copy: [],	
		refresh: false,
		rmALLcount:0,
		rmUEZMcount:0,
		rmCCAcount:0,
		rmUMTNcount:0,
		cusALLcount:0,
		cusUEZMcount:0,
		cusCCAcount:0,
		cusUMTNcount:0,
		pendingRM:localStorage.getItem('pendingRM') !=null ? JSON.parse(localStorage.getItem('pendingRM')):'ALL',
		pendingCUS:localStorage.getItem('pendingCUS') !=null ? JSON.parse(localStorage.getItem('pendingCUS')):'ALL',		
	}	
	
	getListRows = async () => {
		let resp
		await this.server_time(this.time_zone)
		switch(this.props.list_name){
			case 'pending_w_cust':
				let loan_search_criteria = {pending_w_cust: 'true', mode : 'search', alert: false}
				resp = await searchLoan(this.req({loan_search_criteria}))
				if(resp && resp.status === 'success'){ return resp.data.results}							
				break;

			case 'pending_w_rm':
				let loan_appl_search_criteria = {pending_w_rm: 'true'}
      			resp = await searchLoanAppl(this.req({loan_appl_search_criteria}))
				if(resp && resp.status === 'success'){return resp.data}											
		}		
	}

	getListColumns = () => {
		switch(this.props.list_name){
			case 'pending_w_cust':								
				return [
						  {
		        			Header : "ACC PRVDR",
		        			accessor: 'acc_prvdr_code',
		        		  },
						{
							Header: 'FA ID',
							id: 'loan_doc_id',
							accessor: row => <Link to={"fa/view/"+row.loan_doc_id} target="_blank" >{row.loan_doc_id}</Link>
						},
						{
		        			Header : "A/C Number",
							id: 'acc_number',
		        			accessor: row =>  <Link to={"/borrower/indiv/view/"+row.cust_id} target="_blank" >{row.acc_number}</Link>,

		        		},
						{
							Header : "Mobile Number",
							id: 'cust_mobile_num',
							accessor: row => {return row.cust_mobile_num}
		        		},
		        		{
		        			Header : "Biz Name",
		        			id: 'biz_name',
		        			accessor: row => <TooltipLabel full_text={row.biz_name} overlay={row.cust_name} no_reduce={true} />
		        		},{
		        			Header : ()=>(<div className={'tableAmount_centerAlign'}>FA Amount</div>),
		        			id: 'amount',		        			
							accessor: row => <div className={'tableAmount_rightAlign'}>{lbl_amt(row.loan_principal,this.currency_code)}</div>
		        		},
						{
		        			Header : "RM Approved",
		        			id: 'date',
		        			accessor:row => {let appr_date = (row.loan_approved_date == null) ? row.loan_appl_date : row.loan_approved_date
											 return get_time_diff(this.state.server_time.server_time_db, appr_date)
											}
		        		},
		        		{
		        			Header : "No. of Conf. Codes Sent",
		        			accessor: 'otp_count',
		        		}
		        		]
			case 'pending_w_rm':							
				return [
						  {
		        			Header : "ACC PRVDR",
		        			accessor: 'acc_prvdr_code' ,
		        		  },
						{
							Header: 'FA Application ID',
							id: 'loan_appl_doc_id',
							accessor: row => <Link to={"/fa_appl/view/"+row.loan_appl_doc_id} target="_blank">{row.loan_appl_doc_id}</Link>
						},
						{
							Header : "A/C Number",
							id: 'acc_number',
							accessor: row =>  <Link to={"/borrower/indiv/view/"+row.cust_id} target="_blank" >{row.acc_number}</Link>,
		        		},
						{
							Header : "Mobile Number",
							id: 'cust_mobile_num',
							accessor: row => {return row.cust_mobile_num}
		        		},
		        		{
		        			Header : "Biz Name",
		        			id: 'biz_name',
		        			accessor: row => <TooltipLabel full_text={row.biz_name} overlay={row.cust_name} no_reduce={true} />
		        		},{
		        			Header : ()=>(<div className={'tableAmount_centerAlign'}>FA Amount</div>),							
		        			id: 'amount',
		        			accessor: row => <div className={'tableAmount_rightAlign'}>{lbl_amt(row.loan_principal,this.currency_code)}</div>
		        		},
						{
		        			Header : "Applied",
		        			id: 'date',
		        			accessor:row => {
											 return get_time_diff(this.state.server_time.server_time_db, row.loan_appl_date)
											}
		        		},
		        		{
		        			Header : "Pending With",
		        			accessor: 'loan_approver_name' ,
		        		}

		        		]
		}
	}

	refreshList = () => {
            this.setState({refresh: true})
            this.getListData()
    }

	getListData = () => {

		this.getListRows().then((lists) => {			
			let columns = this.getListColumns()
			this.setState({lists, columns, refresh: false})
			this.setState( state => state.copy=lists)
			if(this.props.list_name === "pending_w_cust"){
				this.filterHandler(this.state.pendingCUS)
				this.setState(state => state.cusALLcount = 0)
				this.setState(state => state.cusCCAcount = 0)
				this.setState(state => state.cusUEZMcount = 0)
				this.setState(state => state.cusUMTNcount = 0)
				lists.forEach(element => {            
					if(element.acc_prvdr_code === "UEZM"){
						this.setState(state => state.cusUEZMcount ++)
					}
					else if(element.acc_prvdr_code === "CCA"){
						this.setState(state => state.cusCCAcount ++)
					}
					else if (element.acc_prvdr_code === "UMTN") {
						this.setState(state => state.cusUMTNcount ++)
					}
					this.setState(state => state.cusALLcount ++)
				});
			}
			else if(this.props.list_name === "pending_w_rm"){
				this.filterHandler2(this.state.pendingRM)
				this.setState(state => state.rmALLcount = 0)
				this.setState(state => state.rmCCAcount = 0)
				this.setState(state => state.rmUEZMcount = 0)
				this.setState(state => state.rmUMTNcount = 0)
				lists.forEach(element => {            
					if(element.acc_prvdr_code === "UEZM"){
						this.setState(state => state.rmUEZMcount ++)
					}
					else if(element.acc_prvdr_code === "CCA"){
						this.setState(state => state.rmCCAcount ++)
					}
					else if (element.acc_prvdr_code === "UMTN") {
						this.setState(state => state.rmUMTNcount ++)
					}
					this.setState(state => state.rmALLcount ++)
				});
			}	
		})
		
	}


	componentWillMount() {
		this.getListData()		
    }


	componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.refresh != this.props.refresh){
            this.refreshList()
        }
    }		

	filterHandler2  (val)  {					
		if(val!="ALL"){
			localStorage.setItem('pendingRM',JSON.stringify(val))
			const newArr = ([...this.state.lists.filter(each => each.acc_prvdr_code === val )])			
			this.setState(state => state.copy = newArr)					
		}else{
			localStorage.setItem('pendingRM',JSON.stringify(val))
			const newArr = this.state.lists			
			this.setState(state => state.copy = newArr)						
		}
	}
		
	filterHandler (val) {		
		if(val!="ALL"){
			localStorage.setItem('pendingCUS',JSON.stringify(val))
			const newArr = ([...this.state.lists.filter(each => each.acc_prvdr_code === val )])						
			this.setState(state => state.copy = newArr)		
		}else{
			localStorage.setItem('pendingCUS',JSON.stringify(val))
			const newArr = this.state.lists							
			this.setState(state => state.copy = newArr)
		}
	}		    					

    render(){
		const NullComponent = () => null;
        return (


			<div>
				<div className="card">
				<div className="card-header card-header-icon card-header-primary d-flex">					
						{this.props.list_name == "pending_w_cust" &&
							<div style={{display:'inline-flex'}}>
								<h4 className="card-title text-white tableHead_font no-margin">{this.getListTitle()}</h4>
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingCUS")) == "ALL" ? "darkgray" : ''}} onClick={()=>this.filterHandler("ALL")}>ALL</button>	
									<div className='countABS'>
										<label>{this.state.cusALLcount}</label>
									</div>
								</div>	
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingCUS")) == "UEZM" ? "darkgray" : ''}} onClick={()=>this.filterHandler("UEZM")}>UEZM</button>
									<div className='countABS'>
										<label>{this.state.cusUEZMcount}</label>
									</div>
								</div>
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingCUS")) == "UMTN" ? "darkgray" : ''}} onClick={()=>this.filterHandler("UMTN")}>UMTN</button>
									<div className='countABS'>
										<label>{this.state.cusUMTNcount}</label>
									</div>
								</div>
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingCUS")) == "CCA" ? "darkgray" : ''}} onClick={()=>this.filterHandler("CCA")}>CCA</button>													
									<div className='countABS'>
										<label>{this.state.cusCCAcount}</label>
									</div>
								</div>																																						
								{/* {this.state.refresh && <h2 className="reloadText"><i class="fa fa-refresh"> </i> Refreshing...</h2>} */}						
							</div>
						}
						{this.props.list_name == "pending_w_rm" &&
							<div style={{display:'inline-flex'}}>
								<h4 className="card-title text-white tableHead_font no-margin">{this.getListTitle()}</h4>
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingRM")) == "ALL" ? "darkgray" : ''}} onClick={()=>this.filterHandler2("ALL")}>ALL</button>
									<div className='countABS'>
										<label>{this.state.rmALLcount}</label>
									</div>
								</div>
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingRM")) == "UEZM" ? "darkgray" : ''}} onClick={()=>this.filterHandler2("UEZM")}>UEZM</button>
									<div className='countABS'>
										<label>{this.state.rmUEZMcount}</label>
									</div>
								</div>
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingRM")) == "UMTN" ? "darkgray" : ''}} onClick={()=>this.filterHandler2("UMTN")}>UMTN</button>
									<div className='countABS'>
										<label>{this.state.rmUMTNcount}</label>
									</div>
								</div>
								<div className='countREL'>
									<button className='filterBtns' style={{backgroundColor: JSON.parse(localStorage.getItem("pendingRM")) == "CCA" ? "darkgray" : ''}} onClick={()=>this.filterHandler2("CCA")}>CCA</button>
									<div className='countABS'>
										<label>{this.state.rmCCAcount}</label>
									</div>
								</div>																																
							</div>
						}
					</div>																
					<div className="card-body">
						<div className="table-responsive">
							<ReactTable NoDataComponent={NullComponent} showPaginationBottom={false} minRows = {1}  data={this.state.copy}  
							columns={this.state.columns} 
							resizable={false}  defaultSorted={[{id: "acc_prvdr_code",
							desc: true}]}  />
        				</div>
					</div>
				</div>
			</div>
        	)

    }

	getListTitle = () => {
		switch (this.props.list_name){
			case 'pending_w_cust':
				return "FAs Pending Customer Confirmation"
			case 'pending_w_rm':
				return "FAs Pending RM Approval"
		}
	}
}


export default DisplayList;
