import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {borrowerSearch} from '../../actions/borrower_actions';
import ReactTable from "react-table"; 
import {format_date_tbl,capitalize_first_letter} from '../../helpers/common_helper';
import { Link} from 'react-router-dom';
import {searchLead} from "../../actions/lead_actions";
import {TooltipLabel} from "../common/partial";
import RMNameSplit from '../../helpers/common_helper';






class KycPendingList extends FlowComponent {

	state = {
		columns :[],
		lists : [],
		copy:[],
		kycALLcount:0,
		kycUMTNcount:0,
		kycUEZMcount:0,
		kycCCAcount:0,
		faPendingdisb: localStorage.getItem('kycAuditpending') !=null ? JSON.parse(localStorage.getItem('kycAuditpending')) : 'ALL',
	}

	componentWillMount() {
		let lead_search_criteria = {status : '50_pending_audit'}
      	searchLead(this.req({lead_search_criteria}))
		        .then((response) => {
		        	if(!response){return };
		        	if(response.status === "success"){												
		        		let columns 
		        		columns = [
						// 	{
		        		// 	Header : "Cust ID",
		        		// 	accessor: 'cust_id' ,
		        		// 	Cell: e =>  <Link to={"/borrower/indiv/view/"+e.value} target="_blank" >{e.value}</Link>,
						//
		        		// },
		        		{
		        			Header : "Biz Name",
		        			id: 'biz_name', 
		        			accessor: row => <TooltipLabel full_text={row.biz_name} overlay={row.cust_name} no_reduce={true} />
		        		},
						{
		        			Header : "Reg Date",
		        			id: 'date', 
		        			accessor:row => format_date_tbl(row.lead_date),
		        		},
		        		{
		        			Header : "ACC PRVDR",
		        			accessor: 'acc_prvdr_code' ,
		        		},
		        		{
		        			Header : "A/C Number",
		        			accessor: 'account_num' ,
		        		},
						{
		        			Header : "Mobile Number",
		        			accessor: 'mobile_num' ,
		        		},
		        		{
		        			Header : "RM Name",
							id:"flow_rel_mgr_name",
		        			accessor: row => <div className='rmUpper_case'>{RMNameSplit(row.flow_rel_mgr_name)}</div>,
		        		},
		        		
		        		{
		        			Header : "Action",
							accessor: 'id' ,
		        			Cell: e =>  <Link to={"lead/audit_kyc/"+ e.value} target="_blank" >Audit KYC</Link>,

		        		}

		        		]
						this.setState({columns,lists:response.data})
						this.setState(state => state.copy = response.data)
						this.filterHandler(this.state.faPendingdisb)
						response.data.forEach(element => {
							if(element.acc_prvdr_code === "UEZM"){
								this.setState(state => state.kycUEZMcount ++)
							}
							else if(element.acc_prvdr_code === "CCA"){
								this.setState(state => state.kycCCAcount ++)
							}
							else if (element.acc_prvdr_code === "UMTN") {
								this.setState(state => state.kycUMTNcount ++)
							}
							this.setState(state => state.kycALLcount ++)
						});							       		
		        	}
		        })
		
    }

	filterHandler  (val)  {    
		localStorage.setItem("kycAuditpending",JSON.stringify(val))
		if(val=="ALL"){
			const newArr = this.state.lists
			this.setState(state => state.copy = newArr)			
		}
		else if(val=="UEZM"){						
			const newArr = ([...this.state.lists.filter(each => each.acc_prvdr_code === val)])						
			this.setState(state => state.copy = newArr)
		}
		else if(val=="UMTN"){
			const newArr = ([...this.state.lists.filter(each => each.acc_prvdr_code === val)])			
			this.setState(state => state.copy = newArr)			
		}
		else if(val=="CCA"){
			const newArr = ([...this.state.lists.filter(each => each.acc_prvdr_code === val)])			
			this.setState(state => state.copy = newArr)
		}        
	}



    render(){
        return (

        	<div>
				<div className="card">
					<div className="card-header card-header-icon card-header-primary d-flex">
						<h4 className="card-title text-white no-margin tableHead_font">Leads with Pending KYC Audit</h4>
						<div className='countREL'>
							<button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("kycAuditpending"))=="ALL" ? "darkgray" : ''}} onClick={()=>this.filterHandler("ALL")}>ALL</button>
							<div className='countABS'>
								<label>{this.state.kycALLcount}</label>
							</div>
						</div>
						<div className='countREL'>
							<button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("kycAuditpending"))=="UEZM" ? "darkgray" : ''}} onClick={()=>this.filterHandler("UEZM")}>UEZM</button>	
							<div className='countABS'>
								<label>{this.state.kycUEZMcount}</label>
							</div>
						</div>
						<div className='countREL'>
							<button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("kycAuditpending"))=="UMTN" ? "darkgray" : ''}} onClick={()=>this.filterHandler("UMTN")}>UMTN</button>	
							<div className='countABS'>
								<label>{this.state.kycUMTNcount}</label>
							</div>
						</div>	
						<div className='countREL'>											
							<button className='filterBtns' style={{backgroundColor:JSON.parse(localStorage.getItem("kycAuditpending"))=="CCA" ? "darkgray" : ''}} onClick={()=>this.filterHandler("CCA")}>CCA</button>
							<div className='countABS'>
								<label>{this.state.kycCCAcount}</label>
							</div>
						</div>
					</div>					
					<div className="card-body">
						<div className="table-responsive">							
				<ReactTable  minRows = {1}  data={this.state.copy} resizable={false}  columns={this.state.columns} showPagination={false} defaultSorted={[{id: "biz_name",
              desc: true}]}  />			  
        	</div>
					</div>
				</div>
			</div>
        	)

    }
}


export default KycPendingList;
